import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

const store=new Vuex.Store({
    state:{
        topics:[//tab
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''}
        ],
        isShowMoreMenus:false,//
       
        isLogined:false,//
        unLoginedMenus:{
            login:''
        },
        loginedMenus:{
            center:'',
            create:'',
            msg:'',
            logout:''
        },
        loginname:'',//
        accesstoken:'',//
        authorId:''//id
    },
    getters:{
        // moreMenus://
        moreMenus(state){
            if(state.isLogined){
                return state.loginedMenus
            }else{
                return state.unLoginedMenus
            }
        }
    },
    mutations:{
        //actionsheet
        mutationsIsShowMoreMenus:function(state,payload){
            state.isShowMoreMenus=payload;
        },
        mutationsIsLogined:function(state){
            state.isLogined=true
        },
        //
        mutationsLoginname:function(state,payload){
            state.loginname=payload
        },
        //accesstoken
        mutationsAccesstoken:function(state,payload){
            state.accesstoken=payload
        },
        //id
        mutationsAuthorId:function(state,payload){
            state.authorId=payload
        },
        //
        mutationsLogout:function(state){
            state.isLogined=false;
            state.loginname=''
            state.accesstoken=''
            state.authorId=''
        }
    },
    actions:{}
})
export default store;


