
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

export default {
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
data:function(){
return{
title:'',
content:'',
isShowToast:false,
toastText:'',
toolbars: {
bold: true, // 

header: true, // 



superscript: true, // 
subscript: true, // 
quote: true, // 


link: true, // 
imagelink: true, // 

table: true, // 


htmlcode: true, // html

/* 1.3.5 */




/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true 
}

}
},
methods:{

















addtopic:function(){
var app=this
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev'
}).then(function(res){
app.$router.replace('/topiclist/dev')
}).catch(function(err){


app.isShowToast=true;
app.toastText=''
})
}
}
}



